#loading {
  background: #222428;
  background-size: 163px;
  font: 14px/21px "Monaco", sans-serif;
  color: #999;
  -webkit-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  text-size-adjust: 100%;
  height: 100%;
  min-height: 100%;
  user-select: none;
}
#loading .scene {
  width: 100%;
  height: 100%;
  -webkit-perspective: 600;
  perspective: 600;
  display: flex;
  align-items: center;
}
#loading .scene svg {
  width: 200px;
  height: 200px;
  margin: auto;
}
